const FACTOR = {
  car: 0.2176,
  train: 0.00633,
  avion: 0.14158,
  velo: 0,
};

const deplacementKeys = ['distance-car', 'distance-common', 'distance-bike'];

export const computeTotal = (data: any) => {
  const foodValues = data.filter((d: any) => d.type === 'food');
  const transportValues = data.filter((d: any) => d.type === 'transport');

  let totalCo2Food = 0;
  let totalCo2Transport = 0;
  let totalCo2 = 0;

  console.log('data', data);

  foodValues.forEach((fv: any) => {
    if (fv.factor) {
      totalCo2Food += fv.factor * fv.value;
      totalCo2 += fv.factor * fv.value;
    }
  });

  transportValues.forEach((fv: any) => {
    if (fv.factor) {
      totalCo2Transport += fv.factor * fv.value;
      totalCo2 += fv.factor * fv.value;
    }
  });

  return {
    total: totalCo2,
    food: totalCo2Food,
    transportValues: totalCo2Transport,
  };
};
